import React from 'react';
import PropTypes from 'prop-types';
import usePermission from './usePermission';

/**
 * 
 * @param {*} children component c x l auth
 * @param {*} need permission id cn c  c th s dng chc nng m children ang thc thi
 * @param {*} fallback khi user hin ti khng c permission th n s tr v fallback
 *  
 * @returns JSX.Element
 */

function NeedPermission({
    children, 
    need, 
    fallback,
    permissionJoin = 'or'
}) {

    const isEnoughPermission = usePermission(need, permissionJoin);

    if(!isEnoughPermission) {
        return fallback || <></>;
    }

    return children;
};

NeedPermission.propTypes = {
    children: PropTypes.any,
    need: PropTypes.oneOfType([PropTypes.string, PropTypes.array]),
    fallback: PropTypes.any,
    permissionJoin: PropTypes.string
};

export default NeedPermission;